/* ====================================
   All region in project
   ==================================== */


/* zone global style */

.zone {
    &__header {
        margin-bottom: 10px;
        @include clearfix;
    }
    &__title {
        font-size: 18px;
        line-height: 3.2rem;
        text-transform: uppercase;
        font-weight: bold;
        float: left;
    }
    &__navbar {
        float: right;
        .btn {
            border: none;
            background: #E5E5E5;
            width: 32px;
            height: 32px;
            color: white;
            text-align: center;
            &:hover {
                background: $brand-primary;
            }
            &+.btn {
                margin-left: 10px;
            }
        }
    }
    &__content {
        @include clearfix();
    }
    &--special {
        .zone__content {
            background: #F9EFD6;
            padding: 10px;
        }
    }
    & + .zone {
        margin-top: 15px;
        padding-top: 20px;
        border-top: 1px solid #EEEEEE;
    }
}


/* ads */

.qc {
    @include clearfix();
    margin-bottom: 20px;
    text-align: center;
}
